export class Color {

    r: number;
    g: number;
    b: number;

    constructor(r: number = 0, g: number = 0, b: number = 0) {
        this.r = r;
        this.g = g;
        this.b = b;
    }

    public toString()
    {
        return "rgb(" + [this.r,this.b, this.g].join() + ")"; 
    }

}



